}

.gradient-title {
  background: linear-gradient(to top, #170e31 10%, #402e6b 50%, #1a1530 100%);
}

.gradient-spec-active {
  background: linear-gradient(-45deg, #04d9ff, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-spec-comfort, .offer-container__column:nth-child(2) {
  background: linear-gradient(-45deg, #c8ff00, #170e31 83%);
  background-size: 200% 200%;
}

.gradient-border-right, .main-content__slide::after {
  background: linear-gradient(to right, rgba(42, 53, 20, 0.685) 20%, #332764 50%, #2a3514 80%);
}

.gradient-border-left {
  background: linear-gradient(to left top, #8164ff 40%, rgb(42, 53, 20) 70%);
}

.icon-style {
  border: 1px var(--border-color);
  border-style: none solid;
  height: 100%;
  padding: 0 1.5rem;
  display: flex;
  align-items: center;
}

.shadow-after, .services__title:nth-child(1)::after, .offer-container__title::after {
  transform: rotateX(15deg) translateZ(-10px);
  color: rgba(23, 14, 49, 0.479);
  text-shadow: 5px 0 5px #0b0813, 0 0 40px rgba(125, 248, 255, 0.26), 0 0 20px rgba(204, 255, 0, 0.267);
  z-index: -1;
}

.overflow-hidden {
  overflow-x: auto;
  -webkit-overflow-scrolling: touch;
  scrollbar-width: none;
}
.overflow-hidden::-webkit-scrollbar {
  height: 0;
}

.border-present, .offer-container__thumbnail::before {
  color: rgb(4, 217, 255);
  display: block;
  position: absolute;
  inset: 0.7rem;
  background-image: linear-gradient(to right, currentColor 0%, rgb(200, 255, 0) 8%, transparent 8%, transparent 92%, rgb(200, 255, 0) 92%, currentColor 100%) linear-gradient(to bottom, currentColor 0%, rgb(200, 255, 0) 11%, transparent 11%, transparent 89%, rgb(200, 255, 0) 89%, currentColor 100%);
  background-size: 100% 1px, 100% 1px, 1px 100%, 1px 100%;
  background-position: 0 0, 0 100%, 0 0, 100% 0;
  background-repeat: no-repeat;
}

.link_underline::before, .link_underline::after {
  content: "";
  position: absolute;
  width: 0;
  height: 1px;
  background-color: rgb(204, 255, 0);
  transition: width 0.4s ease-in-out;
}
.link_underline::before {
  right: 0;
  bottom: 0;
}
.link_underline::after {
  bottom: -3px;
  left: 0;
}
.link_underline.active {
  text-shadow: 2px 2px 3px #000;
}
.link_underline.active::before {
  width: 50%;
}
.link_underline.active::after {
  width: 40%;
}
.link_underline:hover {
  color: rgb(4, 217, 255);
  text-shadow: 2px 2px 3px #000;
}
.link_underline:hover::before {
  width: 50%;
}
.link_underline:hover::after {
  width: 40%;
}
.link_underline:hover a {
  opacity: 1;
}

.is-open {
  display: grid;
  grid-template-columns: 1fr;
  width: 100%;
  grid-template-rows: 0fr;
  transition: grid-template-rows 0.4s ease-in-out;
}
.is-open._is-open {
  grid-template-rows: 1fr;
}

.gradient-border-bottom {
  background: linear-gradient(to left top, #8164ff 40%, rgb(42, 53, 20) 70%);
}

@media (min-width: 920.99px) {
  /* FLOW: RIGHT */
  [data-tooltip] {
    position: relative;
  }
  [data-tooltip]::after {
    content: attr(data-tooltip);
    border-radius: 0.6rem;
    border: 1px solid var(--border-color);
    background-color: rgba(35, 52, 59, 0.8705882353);
    position: absolute;
    padding: 1.2rem;
    color: #fff;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
    font-family: "Roboto", sans-serif;
    font-weight: 200;
    font-size: 1.3rem;
    letter-spacing: 0.2em;
    text-transform: uppercase;
    z-index: 9;
    white-space: nowrap;
  }
  [data-tooltip]::before {
    content: "";
    border: 9px solid transparent;
    border-right: 9px solid var(--font-color-active);
    position: absolute;
    z-index: 9;
    margin-left: -20px;
    bottom: 12%;
    opacity: 0;
    visibility: hidden;
    transition: all 0.4s ease-in-out;
  }
  [data-tooltip]:hover::before,
  [data-tooltip]:hover::after {
    visibility: visible;
    opacity: 1;
    display: block;
  }
  [data-tooltip=""]::before,
  [data-tooltip=""]::after {
    display: none !important;
  }
  [data-tooltip][flow^=right]::after {
    top: 50%;
    left: calc(120rem + 10px);
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]::before {
    top: 50%;
    left: 121rem;
    transform: translate(0.5em, -50%);
  }
  [data-tooltip][flow^=right]:hover::before,
  [data-tooltip][flow^=right]:hover::after {
    animation: data-tooltip 300ms ease-out forwards;
  }
  @keyframes data-tooltip {
    to {
      opacity: 1;
      transform: translate(-110rem, -50%);
    }
  }
}
.breadcrumb a:after {
