  }
}
.content-box__column {
  position: relative;
  min-width: 150px;
  max-width: 350px;
}
.content-box__column::before {
  content: "";
  display: block;
  position: absolute;
  box-shadow: 0px 0px 2px 5px rgba(4, 217, 255, 0.4784313725);
  top: -20px;
  left: 30px;
  right: 30px;
  bottom: -20px;
  z-index: -1;
  border-radius: 4px;
  opacity: 0.3;
  transition: all 0.3s ease-in-out;
}
.content-box__column:hover::before {
  top: -10px;
  left: -10px;
  right: -10px;
  bottom: -10px;
  opacity: 0.8;
}
.content-box__column:hover .content-box__image {
  scale: 1.05;
}
.content-box__column:hover .content-box__bg-img {
  transform: translateY(0);
  opacity: 1;
}
.content-box__column:hover .content-box__text {
  transform: translateY(-150%);
}
.content-box__image {
  overflow: hidden;
  transition: all 0.5s ease-in-out;
  height: 100%;
  cursor: pointer;
  border-radius: 4px;
  filter: drop-shadow(0px 0px 10px #392b7c) invert(5%);
}
.content-box__image img {
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  object-fit: cover;
  object-position: center;
  transition: scale 0.4s ease-in-out;
}
.content-box__image img:hover {
  scale: 1.1;
}
.content-box__image svg {
  pointer-events: none;
}
.content-box__bg-img {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  opacity: 0;
